/*
 * @base
 *
 * Rinitialisation de l'apparence des lments HTML
 * ===
 *
 * Ne nous embarassons pas avec des choses inutiles !
 *
 * ## Ressources
 *
 * - http://tantek.com/log/2004/undohtml.css
 * - http://meyerweb.com/eric/tools/css/reset/reset.css
 */



/*
 * 1. vite tout problme de barre de dfilement qui apparaissent puis
 *    disparaissent dans un environnement dynamique.
 * 2. Spcifie de faon arbitraire une famille de fontes gnrique.
 * 3. Garde la mme taille de texte aprs un changement d'orientation,
 *    tout en autorisant l'utilisateur  zoomer s'il en a besoin.
 */
html {
  font-family: sans-serif; /* 2 */
  overflow-y: scroll; /* 1 */
  text-size-adjust: 100%; /* 3 */
}

/*
 * 1. Spcifie de faon arbitraire certaines bases.
 *    cf. :
 *    - https://github.com/h5bp/html5-boilerplate/pull/1343
 *    - https://github.com/10up/sanitize.css/issues/42
 */
body {
  background-color: #fff; /* 1 */
  color: #000; /* 1 */
}

/*
 * 1. Aide les navigateurs  appliquer une taille de police identique
 *     la famille de polices `monospace` quel que soit le cas d'utilisation.
 *    cf. :
 *    - http://meyerweb.com/eric/thoughts/2010/02/12/fixed-monospace-sizing/
 *    - http://neugierig.org/software/chromium/notes/2009/04/monospace-fonts.html
 * 2. Permet  IE de suivre correctement le fonctionnement introduit par `1` dans
 *    le cas particulier o aucune taille de police n'aurait t dfini.
 */
pre,
code,
kbd,
samp {
  font-family: monospace, sans-serif; /* 1 */
  font-size: 1em; /* 2 */
}

/*
 * 1. Dfinit la hauteur de la ligne  un pixel.
 */
hr {
  border-style: solid;
  border-width: 1px 0 0; /* 1 */
  margin-bottom: 0;
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
dl,
table,
figure {
  margin-bottom: 0;
  margin-top: 0;
}

blockquote,
figure {
  margin-left: 0;
  margin-right: 0;
}

dd {
  margin-left: 0;
}

ol,
ul {
  list-style-type: none;
  margin-bottom: 0;
  margin-top: 0;
  padding-left: 0;
}

figure {
  margin: 0;
}

/*
 * 1. Supprime les bordures dfinies dans le fichier `html.css`.
 */
th,
td {
  border: 0; /* 1 */
  padding: 0;
}

/*
 * 1. Prend la couleur de son lment parent.
 */
a {
  color: inherit; /* 1 */
  text-decoration: none;
}

/*
 * 1. Retire l'espace indsirable sous les lments HTML
 *    (`<img>`, `<iframe>`, etc) lorsque ces derniers sont
 *    le seul contenu d'un autre lment HTML.
 *    cf. : http://www.alsacreations.com/astuce/lire/52-espaces-sous-les-images.html
 */
img,
iframe,
object,
svg,
audio,
video {
  vertical-align: bottom; /* 1 */
}

/*
 * 1.
 */
svg {
  fill: currentColor;
  stroke: currentColor;
}

iframe {
  border: 0;
}

/*
 * 1. Ne dessine pas les contours des lments ne pouvant pas
 *    recevoir le focus au clavier ou les lments recevant le focus
 *    par programmation.
 */
[tabindex="-1"]:focus {
  outline: none !important; /* 1 */
}

:lang(fr) > q,
blockquote:lang(fr) {
  quotes: '\0a0' '\0a0' '' '';
}

/*
 * 1.
 */
fieldset {
  border: 0;
  margin-left: 0;
  margin-right: 0;
  min-width: 0; /* 1 */
  padding: 0;
}
body:not(:-moz-handler-blocked) fieldset {
  display: table-cell; /* 1 */
}

/*
 * 1. Utilise un mme modle de boite pour tous les lments d'un formulaire.
 *    Par dfaut, seul l'lment HTML `<select>` a pour valeur `border-box`.
 * 2. Les coins arrondis par dfaut dans iOS non merci !
 * 3. Dfinit de faon arbitraire les marges intrieures des lments HTML.
 *    NOTE : Firefox semble appliquer des marges diffrentes lorsqu'on
 *    applique la dclaration CSS suivante : `border-style: solid;`.
 * 4. Hrite de la taille et famille de la police dfinies par
 *    le contexte (lment parent).
 * 5. 
 * 6. Applique une image de fond (gif de 1x1 transparent) pour viter
 *    l'ombre porte interne dans iOS. `{ box-shadow: none; }` ne suffit pas.
 */
[type=text],
[type=password],
[type=email],
[type=tel],
[type=url],
[type=search],
select,
textarea {
  background-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAAAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==); /* 6 */
  box-sizing: border-box; /* 1 */
  border: 1px solid;
  border-radius: 0; /* 2 */
  font-size: inherit; /* 4 */
  font-family: inherit; /* 4 */
  line-height: normal; /* 5 */
  padding: 1px; /* 3 */
}

/*
 * 1. Contient la largeur de l'lment dans son contenant mme si
 *    ce dernier dispose d'une valeur qui le pousserait  tre plus grand.
 * 2. Loin d'tre la meilleure solution, il s'agit de l'unique faon de
 *    retirer les coins arrondis dans Safari.
 *    Il est fortement recommad d'essayer de rtablir l'aspect de l'lment HTML `<select>`
 *    comme dans l'exemple suivant : http://codepen.io/7studio/pen/jWbdRa
 */
select {
  appearance: none; /* 2 */
  max-width: 100%; /* 1 */
}

/*
 * 1. Permet  un label adjacent de ne pas tre align avec le bas
 *    de l'lment HTML `<textarea>`.
 * 2. Rduit la possibilit de redimensionner l'lment dans le sens vertical.
 */
textarea {
  resize: vertical; /* 2 */
  vertical-align: text-top; /* 1 */
}

/*
 * 1. Garde-fou pour le cas (possible mais stupide) suivant :
 *    `input { display: block; }`.
 *    cf. : http://trac.webkit.org/browser/trunk/Source/WebCore/css/html.css#L410
 */
[type=hidden] {
  display: none !important; /* 1 */
}

/*
 * 1. Les coins arrondis par dfaut dans iOS non merci !
 *    Sauf que, cette dclaration retire tous les syles de l'apparence d'un bouton
 *    aux lments HTML `<input>`.
 *    Pour un rendu homogne, il faut appliquer une image de fond (gif de 1x1 transparent)
 *    aux lments HTML `<button>` pour retirer galement tous les styles.
 */
button,
[type=reset],
[type=submit],
[type=button] {
  background-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAAAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==); /* 1 */
  border-radius: 0; /* 1 */
}

/*
 * 1.
 * 2.
 */
[type=search] {
  appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/*
 * 1.
 */
[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  display: none;
}

/*
 * 1.
 */
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
